import { Link } from 'react-router-dom';

import { convertGenresToSelectOptions } from '../../modules/filters';
import { filmsGenres } from '../../store/films-genres';
import { popularityValues, scoreValues } from '../../store/filters';
import Select from '../UI/Select';
import Toggler from '../UI/Toggler';

import classes from './index.module.scss';

const GENRE_FILTER_TITLE = ':';
const FILM_SCORE_TITLE = ':';
const FILM_POPULARITY_TITLE = ':';
const convertedGenres = convertGenresToSelectOptions(filmsGenres);

interface ISearchFormProps {
  genresOption: string;
  setGenresOption: (option: string) => void;
  scoreValue: string;
  setScoreValue: (option: string) => void;
  popularityValue: string;
  setPopularityValue: (option: string) => void;
  getNextFilm: () => void;
  foundFilmId: number | null;
}

const SearchForm: React.FC<ISearchFormProps> = (props) => {
  const  {
    genresOption,
    setGenresOption,
    scoreValue,
    setScoreValue,
    popularityValue,
    setPopularityValue,
    getNextFilm,
    foundFilmId
  } = props;

  return (
    <form>
      <label className={classes.label}>
        <Select
          title={GENRE_FILTER_TITLE}
          value={genresOption}
          options={convertedGenres}
          disabled={false}
          onChange={setGenresOption}
        />
      </label>

      <div className={classes.line}>
        <Toggler
          title={FILM_SCORE_TITLE}
          name={'score'}
          togglerValue={scoreValue}
          firstLabel={''}
          firstId={'highScore'}
          firstValue={scoreValues.high}
          secondLabel={''}
          secondId={'lowScore'}
          secondValue={scoreValues.low}
          onChange={setScoreValue}
        />
      </div>

      <div className={classes.line}>
        <Toggler
          title={FILM_POPULARITY_TITLE}
          name={'popularity'}
          togglerValue={popularityValue}
          firstLabel={''}
          firstId={'highPopularity'}
          firstValue={popularityValues.high}
          secondLabel={''}
          secondId={'lowPopularity'}
          secondValue={popularityValues.low}
          onChange={setPopularityValue}
        />
      </div>

      {
        foundFilmId !== null &&
        <>
        <div className={classes.line}>
          <button
            className={classes.button}
            type="button"
            onClick={getNextFilm}
          >
             
          </button>
        </div>

        <div className={classes.line}>
          <Link className={classes.button} to={`/film/${foundFilmId}`}></Link>
        </div>
        </>
      }
    </form>
  );
};

export default SearchForm;

